

.black-container{
    background-color:black; 
    height:300px;
    text-align:center;
    padding-top:50px;
}

.black-container h1{
    font-size:40px;
    color:white;
   

}

.black-container p{
    font-size:30px;
    color:white;
}

.btn-light{
    padding:10px;
    font-size:20px;
    margin-top:50px;
}

@media only screen and (min-width: 200px) and (max-width: 767px)  {

    .black-container h1{
        font-size:24px;
        margin-top:-44px;
   
        
    }
    .black-container p{
        font-size:23px;
    }

    .btn-light{
        padding:6px;
        font-size:20px;
        margin-top:10px;
    }
    
   .black-container{
    background-color:black; 
    height:150px;
    text-align:center;
    padding-top:50px;
}

    


}