
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display&family=Varela+Round&display=swap');

.latest-movies{
    margin-top:10px;
    font-size:50px;
    text-align: center;
    padding-bottom: 50px;
    font-family: 'Varela Round', sans-serif;

}

.card{
    background-color: black;
    transition: all 200ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
    position: inherit;
    margin-bottom: 10px;
    
}

.card:hover{
    
    transform: scale(1.115, 1.115);
    
}

.card-title{
    font-size:30px;
    color:white;
}
.card-text{
    font-size:15px;
    color:white;
}

.btn{
    font-size:20px;
    
}

@media only screen and (min-width:200px) and (max-width:767px){
    .latest-movies{
        font-size:28px;
        margin-bottom: -31px;
        font-weight:bold;
    }
   .col{
       flex:none;
   }
}