.banner-side{
margin-top: 80px;
height: 332px;
width: 110%;
background-color: black;
}

.banner-side h1{
    color: white;
    font-size: 35px;
    text-align: center;
    padding-top: 50px;
    padding-right: 10px;
}
.banner-side h2{
    color: white;
    padding-top: 25px;
    font-size: 15px;
    padding-left: 40px;
    padding-right: 30px;
    text-align: left;
    transition: transform 350ms ease-out;
   
   
}
.banner-side:hover{
    transform:translateX(60px);
   
   
    
}

.banner-row{
    margin-top:-50px;
   

}
.banner-button{
    margin-left: 100px;
    margin-top: 20px;
    font-weight: 500;
  
}

@media only screen and (min-width:200px) and (max-width:756px){
    .banner-image{
       max-width:357px;
       margin-top:10px;
    }
    .banner-side{
        width:100%;
        
    }
    .banner-side h1{
       font-size: 30px;
    }
    .banner-button{
        font-size:15px;
    }
}